"use client";

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { FiArrowLeft, FiList, FiClock } from 'react-icons/fi';

interface PredictionRecord {
  id: number;
  price: number;
  size: number;
  rooms: number;
  bathroom: number;
  parking: number;
  furnished: number;
  elevator: number;
  balcony: number;
  floor: number;
  age: number;
  location_score: number;
  prediction_result: number;
  model_used: string;
  timestamp: string;
}

// Apartment category labels
const categoryLabels = ['Budget', 'Standard', 'Premium'];

export default function HistoryPage() {
  const [predictions, setPredictions] = useState<PredictionRecord[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchPredictionHistory = async () => {
      try {
        const response = await fetch('http://localhost:8080/predictions/');
        if (!response.ok) {
          throw new Error('Failed to fetch prediction history');
        }
        const data = await response.json();
        setPredictions(data);
      } catch (err) {
        setError('Error fetching prediction history. Please make sure the backend server is running.');
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchPredictionHistory();
  }, []);

  const formatDateTime = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleString();
  };

  const getModelBadgeColor = (model: string) => {
    switch (model) {
      case 'random_forest': return 'bg-blue-100 text-blue-800';
      case 'knn': return 'bg-purple-100 text-purple-800';
      case 'naive_bayes': return 'bg-green-100 text-green-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getModelDisplayName = (model: string) => {
    switch (model) {
      case 'random_forest': return 'Random Forest';
      case 'knn': return 'KNN';
      case 'naive_bayes': return 'Naive Bayes';
      default: return model;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-6xl mx-auto">
        <div className="flex items-center mb-8">
          <Link href="/" className="flex items-center text-amber-600 hover:text-amber-800">
            <FiArrowLeft className="mr-2" /> Back to Dashboard
          </Link>
        </div>
        
        <div className="bg-white shadow-md rounded-lg overflow-hidden">
          <div className="px-6 py-4 bg-amber-600 text-white">
            <h1 className="text-xl font-semibold">Prediction History</h1>
          </div>

          <div className="p-6">
            {loading ? (
              <div className="flex justify-center items-center h-64">
                <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-amber-500"></div>
              </div>
            ) : error ? (
              <div className="p-6 bg-red-50 border border-red-200 rounded-md">
                <div className="text-red-700">
                  <p>{error}</p>
                </div>
              </div>
            ) : predictions.length === 0 ? (
              <div className="flex flex-col items-center justify-center py-12">
                <FiList className="w-16 h-16 text-gray-300 mb-4" />
                <h3 className="text-xl font-medium text-gray-700">No prediction history</h3>
                <p className="text-gray-500 mt-2">Make some predictions to see them here</p>
                <Link 
                  href="/predict" 
                  className="mt-4 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-amber-600 hover:bg-amber-700"
                >
                  Make a Prediction
                </Link>
              </div>
            ) : (
              <div>
                <div className="mb-6 flex items-center justify-between">
                  <h2 className="text-xl font-semibold flex items-center text-gray-800">
                    <FiClock className="mr-2" /> Recent Predictions
                  </h2>
                  <span className="text-sm text-gray-500">
                    {predictions.length} {predictions.length === 1 ? 'prediction' : 'predictions'}
                  </span>
                </div>

                <div className="overflow-x-auto">
                  <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gray-50">
                      <tr>
                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          ID
                        </th>
                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Timestamp
                        </th>
                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Model
                        </th>
                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Price
                        </th>
                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Size (mÂ²)
                        </th>
                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Rooms
                        </th>
                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Prediction
                        </th>
                      </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                      {predictions.map((prediction) => (
                        <tr key={prediction.id}>
                          <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                            {prediction.id}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                            {formatDateTime(prediction.timestamp)}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className={`px-2 py-1 text-xs font-semibold rounded-full ${getModelBadgeColor(prediction.model_used)}`}>
                              {getModelDisplayName(prediction.model_used)}
                            </span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                            ${prediction.price.toFixed(2)}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                            {prediction.size}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                            {prediction.rooms}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span 
                              className={`px-2 py-1 text-xs font-semibold rounded-md ${
                                prediction.prediction_result === 0 ? 'bg-blue-100 text-blue-800' :
                                prediction.prediction_result === 1 ? 'bg-green-100 text-green-800' :
                                'bg-amber-100 text-amber-800'
                              }`}
                            >
                              {categoryLabels[prediction.prediction_result]}
                            </span>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>

                <div className="mt-8 bg-amber-50 p-6 rounded-lg border border-amber-200">
                  <h3 className="text-lg font-semibold text-amber-800 mb-2">
                    Prediction Analysis
                  </h3>
                  <p className="text-gray-700">
                    The table above shows the history of apartment predictions made using different machine learning models.
                    Each row represents a past prediction with the apartment features used for prediction and the resulting classification.
                  </p>
                  <p className="mt-3 text-gray-700">
                    This history can be used to track prediction patterns over time and evaluate the consistency of different models.
                  </p>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}